||  TinyMCE overrides  ||
\*=====================*/
div.tox-silver-sink {
    z-index: 9999;
}
div.tox-silver-sink .tox-notification {
        display: none;
    }
div.tox-editor-container .tox-menubar button:first-child {
            display: none;
        }
@media screen and (max-width: 639px) {
div.tox-tinymce {
        min-height: 20em
}
